
.to-right-enter,.to-right-leave-to{
	transform: translateX(500px) scale(0) rotateZ(90deg);
	opacity: 0;
}
.to-right-enter-active,.to-right-leave-active{
	transition: all 0.3s;
}
.to-right-leave-active{
	position: relative;
}
.to-right-move{
	transition: all 0.3s;
}




